body {
  color: #444;
  font-family: Open Sans, sans-serif;
}

a {
  color: #191065;
  text-decoration: none;
}

a:hover {
  color: #2f9c95;
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
  font-family: Poppins, sans-serif;
}

.back-to-top {
  visibility: hidden;
  opacity: 0;
  z-index: 996;
  background: #2f9c95;
  border-radius: 4px;
  width: 40px;
  height: 40px;
  transition: all .4s;
  position: fixed;
  bottom: 15px;
  right: 15px;
}

.back-to-top i {
  color: #fff;
  font-size: 28px;
  line-height: 0;
}

.back-to-top:hover {
  color: #fff;
  background: #2c4964;
}

.back-to-top.active {
  visibility: visible;
  opacity: 1;
}

#header {
  z-index: 997;
  padding: 20px 0;
  transition: all .5s;
}

#header.header-scrolled {
  background: #0e3f58;
  padding: 12px 0;
}

#header .logo {
  letter-spacing: 2px;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  font-size: 32px;
  font-weight: 400;
  line-height: 1;
}

#header .logo a {
  color: #fff;
}

#header .logo img {
  max-height: 60px;
}

.navbar {
  padding: 0;
}

.navbar ul {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.navbar li {
  position: relative;
}

.navbar > ul > li {
  padding: 10px 0 10px 24px;
}

.navbar a, .navbar a:focus {
  color: #ffffffb3;
  white-space: nowrap;
  justify-content: space-between;
  align-items: center;
  padding: 0 3px;
  font-family: Poppins, sans-serif;
  font-size: 15px;
  font-weight: 600;
  transition: all .3s;
  display: flex;
  position: relative;
}

.navbar > ul > li > a:before {
  content: "";
  visibility: hidden;
  background-color: #fff;
  width: 0;
  height: 2px;
  transition: all .3s ease-in-out;
  position: absolute;
  bottom: -5px;
  left: 0;
}

.navbar a:hover:before, .navbar li:hover > a:before, .navbar .active:before {
  visibility: visible;
  width: 100%;
}

.navbar a:hover, .navbar .active, .navbar .active:focus, .navbar li:hover > a {
  color: #fff;
}

.mobile-nav-toggle {
  color: #fff;
  cursor: pointer;
  font-size: 28px;
  line-height: 0;
  transition: all .5s;
  display: none;
}

@media (width <= 991px) {
  .mobile-nav-toggle {
    display: block;
  }

  .navbar ul {
    display: none;
  }
}

.navbar-mobile {
  z-index: 999;
  background: #1c2f41e6;
  transition: all .3s;
  position: fixed;
  inset: 0;
  overflow: hidden;
}

.navbar-mobile .mobile-nav-toggle {
  position: absolute;
  top: 15px;
  right: 15px;
}

.navbar-mobile ul {
  background-color: #fff;
  padding: 10px 0;
  transition: all .3s;
  display: block;
  position: absolute;
  inset: 55px 15px 15px;
  overflow-y: auto;
}

.navbar-mobile > ul > li {
  padding: 0;
}

.navbar-mobile a, .navbar-mobile a:focus {
  color: #2c4964;
  padding: 10px 20px;
  font-size: 15px;
}

.navbar-mobile a:hover, .navbar-mobile .active, .navbar-mobile li:hover > a {
  color: #191065;
}

#hero {
  background-color: #0000;
  background-image: linear-gradient(45deg, #111b39cc 100%, #cfcaebb3 0%), url("hero-bg.3ccc9171.jpg");
  background-position: 0 0, center;
  background-repeat: repeat, no-repeat;
  background-size: cover;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  width: 100%;
  height: 100vh;
}

#hero .container, #hero .container-fluid {
  padding-top: 84px;
}

#hero h1 {
  color: #fff;
  margin: 0;
  font-size: 52px;
  font-weight: 700;
  line-height: 64px;
}

#hero h2 {
  color: #fffc;
  margin: 10px 0 0;
  font-size: 20px;
}

#hero .btn-get-started {
  letter-spacing: 1px;
  color: #fff;
  background: none;
  border: 2px solid #fff;
  border-radius: 5px;
  margin-top: 30px;
  padding: 10px 28px;
  font-family: Poppins, sans-serif;
  font-size: 16px;
  font-weight: 500;
  transition: all .5s;
  display: inline-block;
}

#hero .btn-get-started:hover {
  color: #fff;
  background: #2f9c95;
  border-color: #2f9c95;
}

#hero .animated {
  animation: 2s ease-in-out infinite alternate-reverse both up-down;
}

@media (width >= 1200px) {
  #hero {
    background-attachment: fixed;
  }
}

@media (width <= 991px) {
  #hero {
    text-align: center;
  }

  #hero .container, #hero .container-fluid {
    padding-top: 68px;
  }

  #hero .animated {
    animation: none;
  }

  #hero .hero-img {
    text-align: center;
  }

  #hero .hero-img img {
    width: 50%;
  }
}

@media (width <= 768px) {
  #hero h1 {
    font-size: 26px;
    line-height: 36px;
  }

  #hero h2 {
    font-size: 18px;
    line-height: 24px;
  }

  #hero .hero-img img {
    width: 60%;
  }
}

@media (width <= 575px) {
  #hero .hero-img img {
    width: 80%;
  }
}

@media (width <= 575px), (height <= 600px) {
  #hero {
    height: auto;
    padding: 120px 0 60px;
  }
}

@keyframes up-down {
  0% {
    transform: translateY(10px);
  }

  100% {
    transform: translateY(-10px);
  }
}

section {
  padding: 100px 0;
  overflow: hidden;
}

.section-bg {
  background-color: #f9f8ff;
}

.section-title {
  text-align: center;
  padding-bottom: 30px;
}

.section-title h2 {
  text-transform: uppercase;
  margin-bottom: 20px;
  padding-bottom: 20px;
  font-size: 32px;
  font-weight: bold;
  position: relative;
}

.section-title h2:after {
  content: "";
  background: #2f9c95;
  width: 50px;
  height: 3px;
  display: block;
  position: absolute;
  bottom: 0;
  left: calc(50% - 25px);
}

.section-title p {
  margin-bottom: 0;
}

.about {
  align-items: flex-start;
  padding: 120px 0;
}

.about .content h3 {
  color: #2c4964;
  font-size: 32px;
  font-weight: 600;
}

.about .about-center {
  align-items: center;
  display: flex;
}

.about .content ul {
  padding: 0;
  list-style: none;
}

.about .content ul li {
  padding-bottom: 10px;
}

.about .content ul i {
  color: #2f9c95;
  padding-right: 4px;
  font-size: 20px;
}

.about .content p:last-child {
  margin-bottom: 0;
}

.about img {
  justify-content: center;
