.testimonials .testimonials-carousel, .testimonials .testimonials-slider {
  overflow: hidden;
}

.testimonials .testimonial-item {
  box-sizing: content-box;
  text-align: center;
  min-height: 320px;
  padding: 20px;
}

.testimonials .testimonial-item .testimonial-img {
  object-fit: cover;
  z-index: 2;
  border: 6px solid #fff;
  border-radius: 50%;
  width: 150px;
  height: 150px;
  margin-left: auto;
  margin-right: auto;
  display: block;
  position: relative;
  box-shadow: 0 2px 15px #0000001a;
}

.testimonials .testimonial-item h3 {
  text-align: center;
  color: #2c4964;
  margin-top: 15px;
  font-size: 18px;
  font-weight: bold;
}

.testimonials .testimonial-item h4 {
  color: #999;
  text-align: center;
  font-size: 14px;
}

.testimonials .swiper-pagination {
  margin-top: 20px;
  position: relative;
}

.testimonials .swiper-pagination .swiper-pagination-bullet {
  opacity: 1;
  background-color: #fff;
  border: 1px solid #2f9c95;
  width: 12px;
  height: 12px;
}

.testimonials .swiper-pagination .swiper-pagination-bullet-active {
  background-color: #2f9c95;
}

@media (width <= 767px) {
  .testimonials {
    margin: 30px 10px;
  }
}

.contact .info-box {
  color: #444;
  text-align: center;
  background: #fff;
  padding: 20px 0 30px;
  box-shadow: 0 0 30px #d6d7d899;
}

.contact .info-box i {
  color: #2f9c95;
  border-radius: 50%;
  padding: 8px;
  font-size: 32px;
}

.contact .info-box h3 {
  color: #2c4964;
  margin: 10px 0;
  font-size: 20px;
  font-weight: 700;
}

.contact .info-box p {
  margin-bottom: 0;
  padding: 0;
  font-size: 14px;
  line-height: 24px;
}

.contact .php-email-form {
  background: #fff;
  padding: 30px;
  box-shadow: 0 0 30px #d6d7d899;
}

.contact .php-email-form .error-message {
  color: #fff;
  text-align: left;
  background: #ed3c0d;
  padding: 15px;
  font-weight: 600;
  display: none;
}

.contact .php-email-form .error-message.d-block {
  display: block;
}

.contact .php-email-form .sent-message {
  color: #fff;
  text-align: center;
  background: #18d26e;
  padding: 15px;
  font-weight: 600;
  display: none;
}

.contact .php-email-form .sent-message.d-block {
  display: block;
}

.contact .php-email-form .loading {
  text-align: center;
  background: #fff;
  padding: 15px;
  display: none;
}

.contact .php-email-form .loading.d-block {
  display: block;
}

.contact .php-email-form .loading:before {
  content: "";
  border: 3px solid #18d26e;
  border-top-color: #eee;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  margin: 0 10px -6px 0;
  animation: 1s linear infinite animate-loading;
  display: inline-block;
}

.contact .php-email-form input, .contact .php-email-form textarea {
  box-shadow: none;
  border-radius: 5px;
  font-size: 14px;
}

.contact .php-email-form input:focus, .contact .php-email-form textarea:focus {
  border-color: #191065;
}

.contact .php-email-form input {
  padding: 10px 15px;
}

.contact .php-email-form textarea {
  padding: 12px 15px;
}

.contact .php-email-form button[type="submit"] {
  color: #fff;
  background: #2f9c95;
  border: 0;
  border-radius: 5px;
  padding: 10px 24px;
  transition: all .4s;
}

.contact .php-email-form button[type="submit"]:hover {
  background: #7b27d8;
}

@keyframes animate-loading {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

